.slider {
  cursor: pointer;
  background-image: linear-gradient(to bottom right, #a3a8f0, #696fdd);
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider, input:focus + .slider {
  background-image: linear-gradient(to bottom right, #a3a8f0, #696fdd);
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

